@import url(https://fonts.googleapis.com/css2?family=Farro&family=Luckiest+Guy&display=swap);
* {
  box-sizing: border-box;
}

body,
h1 {
  margin: 0;
  font-family: "Farro", sans-serif;
  text-align: center;
  background-color: #08aeea;
  background-image: linear-gradient(to right, #08aeea 0%, #2af598 100%);
}

input {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border: 2px solid #34656d;
  border-radius: 10px;
  margin: 0 0 2rem;
}

h1 {
  font-family: "Luckiest Guy", cursive;
  font-size: 3rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #02475e;
  margin: 2rem 0;
}

.error {
  font: size 1.25rem;
  color: #af0000;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: transparent;
}

.flow-content > * + * {
  margin-top: 1.5rem;
}

.card {
  display: inline-block;
  width: 18rem;
  margin: 1rem;
  padding: 1rem 0.5rem;
  background: #9ef2e2;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
  transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
  text-align: center;
  border-radius: 0.75rem;
}

.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.card h2 {
  font-size: 1.35rem;
  font-weight: 700;
  color: #5d6c6b;
}

.card p {
  font-weight: 400;
}

